import React,{useEffect, useState} from 'react';
import moment from 'moment';
import 'moment/locale/ko';
import './App.css';

function App() {
  const nowTime = moment().format('YYYYMMDD HH:mm:ss')
  
  const [pageTime,pageTimeChange] = useState(moment().format('YYYY MM DD  HHmmss'));
  useEffect(function(){
    setInterval(function(){//1 state   
      // console.log('1s');
      pageTimeChange(moment().format('YYYY MM DD  HHmmss'));
    },1000)  
  },[pageTime])  
  const [date1YYYY,date1YYYYChange] = useState('');
  const [date1MM,date1MMChange] = useState('');
  const [date1DD,date1DDChange] = useState('');

  const [date2YYYY,date2YYYYChange] = useState('');
  const [date2MM,date2MMChange] = useState('');
  const [date2DD,date2DDChange] = useState('');

  const [finalCount,finalCountChange] = useState('');
  
  function countDate(){    
    if(date1YYYY == "" || null || undefined || 0 || NaN){
      alert('  .')
    }  
    else if(date2YYYY == "" || null || undefined || 0 || NaN){
      alert('  .')
    }  
    else if(date1MM == "" || null || undefined || 0 || NaN){
      alert('  .')
    }  
    else if(date2MM == "" || null || undefined || 0 || NaN){
      alert('  .')
    }  
    else if(date1DD == "" || null || undefined || 0 || NaN){
      alert('  .')
    }  
    else if(date2DD == "" || null || undefined || 0 || NaN){
      alert('  .')
    }  
    else if(date1YYYY>2090){
      alert(' 2090  .');
    }
    else if(date1YYYY<1900){
      alert(' 1900  .');
    }
    else if(date1MM>12){
      alert(' 1 12 .')
    }
    else if(date1MM<1){
      alert(' 1 12 .')
    }
    else if(date1DD>31){
      alert(' 1 31 .')
    }
    else if(date1DD<1){
      alert(' 1 31 .')
    }
    
    else if(date2YYYY>2090){
      alert(' 2090  .');
    }
    else if(date2YYYY<1900){
      alert(' 1900  .');
    }
    else if(date2MM>12){
      alert(' 1 12 .')
    }
    else if(date2MM<1){
      alert(' 1 12 .')
    }
    else if(date2DD>31){
      alert(' 1 31 .')
    }
    else if(date2DD<1){
      alert(' 1 31 .')
    }

    

    else{
      const countdate1 = new Date(date1YYYY,date1MM,date1DD);
      const countdate2 = new Date(date2YYYY,date2MM,date2DD);
      const minusCount = countdate2.getTime() - countdate1.getTime();
      finalCountChange(minusCount / 1000 / 60 / 60 / 24);   
      console.log(finalCount);
    }    
  };
  return (
    <div className="App">
      <div className='container'>
        <h1> </h1>
        <h2><br/>{pageTime} (GMT+9) .</h2>
        <div className='inner'>
          <form className='date_form'>
            <div className='top'>
              <input type="number" min="0" max="9999" maxLength="4" placeholder='' value={date1YYYY} onChange={(e)=>{date1YYYYChange(e.target.value)}}/>
              <span className='blank'></span>
              <input type="number" maxLength="2" placeholder='' value={date1MM} onChange={(e)=>{date1MMChange(e.target.value)}}/>
              <span className='blank'></span>
              <input type="number" maxLength="2" placeholder='' value={date1DD} onChange={(e)=>{date1DDChange(e.target.value)}}/>
              <span className='to'></span>
            </div>                        
            <div className='bottom'>
              <input type="number" maxLength="4" placeholder='' value={date2YYYY} onChange={(e)=>{date2YYYYChange(e.target.value)}}/>
              <span className='blank'></span>
              <input type="number" maxLength="2" placeholder='' value={date2MM} onChange={(e)=>{date2MMChange(e.target.value)}}/>
              <span className='blank'></span>
              <input type="number" maxLength="2" placeholder='' value={date2DD} onChange={(e)=>{date2DDChange(e.target.value)}}/>
              <span className='from'></span>
            </div>            
            
            <button type='button' className='btn_count' onClick={countDate}></button>
          </form>                        
                                    
          <div className='result'>
            <p>{finalCount-1} ( {finalCount}) !</p>
          </div>
        </div>        
      </div>
      
    </div>
  );
}

export default App;
